.hot-goods-wrap {
  padding: 10px 0 20px 10px;
  background-color: #fff;
}

.hot-goods-wrap .goods-item .thumb {
  width: 140px;
  height: 140px;
}

.hot-goods-wrap .goods-item .detail {
  position: relative;
  font-size: 14px;
  height: 140px;
  margin-left: 150px;
}

.hot-goods-wrap .goods-item .title {
  font-size: 14px;
}

.hot-goods-wrap .goods-item .price {
  margin: 0;
  padding-bottom: 0;
}

.hot-goods-wrap .meta {
  position: relative;
  zoom: 1;
}

.hot-goods-wrap .meta:after {
  content: "";
  display: table;
  clear: both;
}

.hot-goods-wrap .recommend {
  font-size: 12px;
  line-height: 14px;
}

.hot-goods-wrap .from-shop {
  position: absolute;
  bottom: 10px;
  width: 100%;
  padding-right: 20px;
  box-sizing: border-box;
}

.hot-goods-wrap .from-shop a {
  display: block;
  color: silver;
  font-size: 12px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.hot-shop-wrap {
  background-color: #f4f4f4;
}

.hot-shop-wrap .avatar {
  position: relative;
  float: left;
  width: 56px;
  height: 56px;
  border-radius: 50%;
  overflow: hidden;
  z-index: 995;
}

.hot-shop-wrap .avatar img {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  max-width: 100%;
  max-height: 100%;
  margin: auto;
}

.hot-shop-wrap .info {
  height: 56px;
  margin-left: 66px;
}

.hot-shop-wrap .shop-name {
  font-size: 14px;
  padding-top: 12px;
  position: relative;
  height: 18px;
}

.hot-shop-wrap .shop-name .name {
  float: left;
  margin-right: 10px;
}

.hot-shop-wrap .follows {
  padding-top: 5px;
  margin-left: 0;
}

.hot-shop-wrap .shop-item {
  padding: 12px;
  background-color: #fff;
  margin-bottom: 10px;
}

.hot-shop-wrap .shop-item:last-child {
  margin-bottom: 0;
}

.hot-shop-wrap .shop-images {
  margin-right: 0;
}

.hot-shop-wrap .shop-images li {
  border: 1px solid #efefef;
}

.hot-shop-wrap .icon-top1,
.hot-shop-wrap .icon-top2,
.hot-shop-wrap .icon-top3 {
  display: inline-block;
  width: 34px;
  height: 15px;
  background: url("/v2/image/yzapp/h5/TOP1@2x.png") no-repeat 50%;
  background-size: 100% auto;
}

.hot-shop-wrap .icon-top2 {
  background-image: url("/v2/image/yzapp/h5/TOP2@2x.png");
}

.hot-shop-wrap .icon-top3 {
  background-image: url("/v2/image/yzapp/h5/TOP3@2x.png");
}

@-webkit-keyframes loader {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(1turn);
    transform: rotate(1turn);
  }
}

@keyframes loader {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(1turn);
    transform: rotate(1turn);
  }
}
</style>
