.category-guid {
  font-size: 0;
  margin-bottom: 10px;
}

.list-finished,
.notice {
  padding: 10px;
}

.notice {
  display: block;
  font-size: 12px;
  line-height: 16px;
  color: #f90;
  background-color: #ffc;
  letter-spacing: 0.4px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.activity {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-top: 10px;
  overflow: hidden;
  font-size: 0;
  position: relative;
}

.activity:after {
  content: " ";
  display: block;
  padding-top: 40%;
}

.activity .item {
  width: 50%;
}

.activity .item:first-child {
  position: relative;
}

.activity .item:first-child:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  box-sizing: border-box;
  width: 200%;
  height: 200%;
  -webkit-transform: scale(0.5);
  transform: scale(0.5);
  -webkit-transform-origin: left top;
  transform-origin: left top;
  pointer-events: none;
  border-right: 1px solid #e5e5e5;
}

.fadeIn {
  -webkit-animation-duration: 1s;
  animation-duration: 1s;
  -webkit-animation-name: a;
  animation-name: a;
}

@-webkit-keyframes a {
  0% {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes a {
  0% {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
</style>
